import React from 'react'

import Baby from './baby.png'
import Incubator from './item1.png'

class Home extends React.Component{
  constructor(){
    super()
    this.state = {
      count : 0,
      click : 1,
      incubators: 0,
      incubatorprice: 20,
    }
    this.ballClick = this.ballClick.bind(this)
    this.buyIncubator = this.buyIncubator.bind(this)
    this.autoClick = this.autoClick.bind(this)
    setInterval(this.autoClick,500)
  }

  autoClick(){
    this.setState(prev =>{
      return {
        count: prev.count + this.state.incubators
      }
    })
  }

  ballClick(){
    this.setState(prev =>{
      return {
        count: prev.count + this.state.click
      }
    })
  }

  buyIncubator(){
    if (this.state.count >= this.state.incubatorprice){
      this.setState(prev =>{
        return {
          incubators: prev.incubators + 1,
          count: prev.count - prev.incubatorprice,
          incubatorprice: Math.round(20 * Math.pow(1.15, prev.incubators))
        }
      })
    }
  }


  render(){
    if (this.state.count >= this.state.incubatorprice){
      var poor = true
    }
    else{
      var poor = false
    }
    return (
      <div className="main">
        <h2>Children</h2>
        <p>You have {this.state.count} babies.</p>
        <img src={Baby} id='baby' onClick={this.ballClick}></img>
        <br></br>
        <div className='item' onClick={this.buyIncubator} style={{
          opacity: poor ? '1' : '0.5'
        }}>
          <img src={Incubator}></img>
          <div>
            <p>Incubators: {this.state.incubators}</p>
            <p>Cost: {this.state.incubatorprice} Babies </p>
          </div>
        </div>
      </div>
    )
  }
}



export default Home
